					this.block.sort(function(a,b){return parseFloat(b.danmu)-parseFloat(a.danmu)})
				}else if(ee==0){
					this.block=JSON.parse(JSON.stringify(this.data_list[this.$route.query.keyword]));
				}
			},
			eb:function(ee){
				this.$set(this.num,1,ee)
				if(ee==0){
				this.block=JSON.parse(JSON.stringify(this.data_list[this.$route.query.keyword]));
				}else if(ee==1){
					this.block=JSON.parse(JSON.stringify(this.data_list[this.$route.query.keyword])).filter(function(a){
						return a.timeme<10
					})
				}else if(ee==2){
					this.block=JSON.parse(JSON.stringify(this.data_list[this.$route.query.keyword])).filter(function(a){
						return a.timeme<30&&a.timeme>=10
					})
				}else if(ee==3){
					this.block=JSON.parse(JSON.stringify(this.data_list[this.$route.query.keyword])).filter(function(a){
						return a.timeme<60&&a.timeme>=30
					})
				}else if(ee==4){
					this.block=JSON.parse(JSON.stringify(this.data_list[this.$route.query.keyword])).filter(function(a){
						return a.timeme>60
					})
				}
			},
			ec:function(ee){this.$set(this.num,2,ee)}
		}
	}
</script>

