<template>
	<div class="home-wrap" style="width: 980px;margin:0 auto;">
		<div class="home from">
			<div class="home-logo">
			</div>
			<div class="home-input clearfix">
				<div class="type" @click="checked=!checked">
					<span class="default">
						{{spa_tes}}
					</span>
				</div>
				<ul class="list" v-if="checked">
					<li v-for="(item,index) in first_input" @click="chn(index)">{{item}}</li>
				</ul>
				<div class="input-suggest">
					<input type="text" v-model="search" class="content" placeholder="搜索你想要的内容" @keydown.enter="voute_push()"/>
				</div>
				<a href="javascript:void(0)" class="searchBtn" @click="voute_push">搜索</a>
			</div>
			<div class="home-tips">
				<ul class="tips-link">
					<li class="tip-item">
						<a href="javascript:void(0)">
							<i class="a"></i>
							<span>动画番剧</span>
						</a>
					</li>
					<li class="tip-item">
						<a href="javascript:void(0)">
							<i class="a"></i>
							<span>最新话题</span>
						</a>
					</li>
					<li class="tip-item">
						<a href="javascript:void(0)">
							<i class="a"></i>
							<span>全站排行</span>
						</a>
					</li>
				</ul>
			</div>
			<div class="home-suggest clearfix">
				<div class="hot-search">
					<div class="title">
						<i class="title-icon hot"></i>
						热门搜索
						<span class="subtitle">Daily Hot</span>
					</div>
					<ul class="hotlist">
						<li class="item" v-for="(item,index) in a">
							<span class="num special">{{index+1}}</span>
							<span class="word">{{item}}</span>
						</li>
					</ul>
					<ul class="hotlist">
						<li class="item" v-for="(item,index) in b">
							<span class="num special">{{index+11}}</span>
							<span class="word">{{item}}</span>
						</li>
					</ul>
				</div>
				<div class="history no-bg">
					<div class="title">
						<i class="title-icon time"></i>
						搜索历史
						<span class="delete-history">
							<i class="icon-garbage"></i>
							清空
						</span>
						<span class="subtitle">History</span>
					</div>
					<span>没有历史记录</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueRouter from 'vue-router'
	Vue.use(VueRouter)
	export default{
		data:function(){
			return {
				checked:false,
				search:"",
				spa_tes:'综合',
				zhong:"",
				first_input:["视频","番剧","影视","直播","专栏","话题","用户","相簿"],
				second:["动画番剧","最新话题","全站排行"],
				a:["shinyruo","qita","番茄","吹落的树叶","jojo","我们不能是","巴拉巴拉熊猫血","CXXX","猛龙","白给"],
				b:["twice","lpl","郭德纲","高校","闺蜜额","陪你到世界之巅","hehe","none","王贵","lllll"]
			}
		},
		created:function(){
			document.title=" - 搜索结果 -"
		},
		methods:{
			chn:function(aaa){
				this.zhong=this.first_input[aaa];
				this.first_input[aaa]=this.spa_tes;
				this.spa_tes=this.zhong;
				this.checked=false
			},
			voute_push:function(){
				if(this.search){
					this.$router.push({
						path: 'all',
						query: {
							keyword: this.search
						}
					})
					document.title=this.search+" - 搜索结果 -"
				}else{
					alert("请输入  shinyruo")
				}
			}
		}
	}
</script>

<style>
	.clearfix:after{
		content: "";
		display: block;
		overflow: hidden;
		height: 0;
		clear: both;
		visibility: hidden;
	}
	.clearfix{
		zoom: 1;
	}
	html{
		font:12px Helvetica Neue,Helvetica,Arial,Microsoft Yahei,Hiragino Sans GB,Heiti SC,WenQuanYi Micro Hei,sans-serif
	}
	.home-logo{
		text-align: center;
		margin: 40px auto 20px;
		width: 284px;
		height: 68px;
		background: transparent url() no-repeat 50%;
	}
	.home-input{
		width: 632px;
		margin: 0 auto 15px;
		position: relative;
	}
	 .home-wrap .home-input .type:hover{
		border: 1px solid #aaa;
		background-image:linear-gradient(0deg,#f8f8f8,#fff);
		box-shadow: 0 2px 4px rgba(0,0,0,.16);
	}
	.home-wrap .home-input .type{
		width: 100px;
		height: 40px;
		line-height: 40px;
		border-radius:4px 0 0 4px;
		text-align: center;
		float: left;
		transition: border 1s linear;
		vertical-align: middle;
		background: #FFFFFF;
		cursor: pointer;
		padding:0 7px;
		border:1px solid #ccd0d7
	}
	.home-wrap .home-input .list{position:absolute;background:#fff;border:1px solid #ccd0d7;border-top:0;z-index:5000;border-radius:0 0 4px 4px;top:40px}
	.home-wrap .home-input .list li{cursor:pointer;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;width:114px;text-align:center;height:40px;line-height:40px}
	.home-wrap .home-input .list li:hover{background-color:#e5e9ef}
	.home-wrap .home-input .content{float:left;border:1px solid #ddd;border-left:0;border-right:0;padding:0 10px;width:347px;height:40px;color:#222;width:374px}
	.home-wrap .home-input .searchBtn{float:left;text-align:center;line-height:40px;font-size:16px;font-weight:bolder;background-color:#00a1d6;color:#fff;border:1px solid #008cd2;width:120px;height:40px;border-radius:0 4px 4px 0}
	ul,li,ol{
		padding:0;
		margin: 0;
		list-style: none;
	}
	.home-wrap .home-tips .tips-link .tip-item{
		display:inline-block;
		width:150px;
		height:24px;
		line-height:24px;
		vertical-align:middle;
		text-align:center;
	}
	.home-wrap .home-tips .tips-link .tip-item a{outline:0;color:#00a1d6;text-decoration:none;cursor:pointer}
	.home-wrap .home-tips .tips-link{
		text-align: center;
	}
	.home-wrap .home-tips .tips-link span{
		line-height:24px;
		vertical-align:middle;
		padding:4px 5px;
		}
	.home-wrap .home-suggest{
		width:980px;
		padding-top:81px;
		margin-top:-5px;
		margin-bottom:45px;
		background:transparent url() no-repeat top;
		}
	.home-wrap .home-suggest .hot-search{
		float:left;width:390px;
		height:340px;
		padding:20px 10px 20px 20px;
		margin-right:10px;
		margin-left:155px;
		border:1px solid #ddd;
		}
	.home-wrap .home-suggest .title
	{
		position:relative;
		font-size:20px;
		margin-left:23px;
		}
	.home-wrap .home-suggest .title .title-icon
	{
		position:absolute;
		display:inline-block;
		right:100%;width:34px;
		height:28px;
		background:url() no-repeat;
		}
	.home-wrap .home-suggest .title .subtitle
	{
		font-size:12px;
	color:#aaa;
	display:block;
	margin-bottom:20px;
	}
	.home-wrap .home-suggest .hotlist
	{width:160px;
	float:left;
	margin-right:35px}
	.home-wrap .home-suggest .hotlist:last-child{margin-right:0}
	.home-wrap .home-suggest .hotlist .item{
		width:160px;
		border-bottom:1px dotted #eee;
		line-height:17px;
		padding-bottom:5px;
		margin-bottom:5px;
		float:left;
		}
	.home-wrap .home-suggest .hotlist .num{
		display:inline-block;
		vertical-align:middle;
		width:16px;
		color:#fff;
		line-height:16px;
		font-family:Arial;
		background-color:#aaa;
		margin-right:8px;
		text-align:center;
		}
	.home-wrap .home-suggest .hot-search .item .num.special{background-color:#f489ad}
	.home-wrap .home-suggest .title .title-icon.hot{background-position:-1px -1px}
	.home-wrap .home-suggest .title .title-icon.time{background-position:-1px -38px}
	.home-wrap .home-suggest .history{
		float:left;
		width:175px;
		height:340px;
		padding:20px;
		border:1px solid #ddd;
		background:transparent url() no-repeat 100% 100%;
		}
</style>


