<template>
	<div class="search">
	<b-header></b-header>
	<router-view></router-view>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueRouter from 'vue-router'
	import header from './header'
	import b_body from './b_body'
	import none from './none'
	Vue.use(VueRouter)
	var router = new VueRouter({
		routes: [
		{
			path: '/all',
			name: 'HelloWorld',
			component:b_body,
		},
		{
			path: '/',
			name: 'Hello',
			component:none,
		}
		]
	})

	export default{
		router:router,
		components:{
			"b-header":header,
		}
	}
</script>

<style>
</style>


